
class GlobalVars {
    constructor() {
        this._liveclass = {};
        this._serverUrl = 'http://cltcbd.xyz/api/';
        this._liveClassServerUrl = 'http://class.cltcbd.xyz/';
    }


    get liveclass() {
        return this._liveclass;
    }

    set liveclass(value) {
        this._liveclass = value;
    }


    get serverUrl() {
        return this._serverUrl;
    }

    set serverUrl(value) {
        this._serverUrl = value;
    }

    get liveClassServerUrl() {
        return this._liveClassServerUrl;
    }

    set liveClassServerUrl(value) {
        this._liveClassServerUrl = value;
    }
}

export const $liveClassGlobals = new GlobalVars();

